@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
}
.about,
.experience,
.education,
.projects,
.skills,
.blogs,
.resume {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}
.mainContent {
  margin-left: 15rem;
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABoAAAAaCAAAAACMIRMSAAAAmElEQVR4AX2RwQ4EIQhD/f9ftYW7p6U0buYiEmLG5fGUXRlELda2eyEJ6mDFea36Kf8RZ1egUjFTOFTtn4RSFN6Uu7Pq0bu7iFx8U/ZEh3yyyEk1fFIy+CXiXB+Ve3TdGVxuN6XTeRo0Y6d38aMLH8ZvtBXjDK/BtayAreM0dBdXZqcsdq+cJv/9f/3l286UK68zZVKX6V0/HECKK51+49AAAAAASUVORK5CYII=) !important;
}
html {
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABoAAAAaCAAAAACMIRMSAAAAmElEQVR4AX2RwQ4EIQhD/f9ftYW7p6U0buYiEmLG5fGUXRlELda2eyEJ6mDFea36Kf8RZ1egUjFTOFTtn4RSFN6Uu7Pq0bu7iFx8U/ZEh3yyyEk1fFIy+CXiXB+Ve3TdGVxuN6XTeRo0Y6d38aMLH8ZvtBXjDK/BtayAreM0dBdXZqcsdq+cJv/9f/3l286UK68zZVKX6V0/HECKK51+49AAAAAASUVORK5CYII=) !important;
}

.headName {
  font-weight: bold;
  margin: 0;
  position: absolute;
  top: 10%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 2rem;
  color: lightblue;
}

.card {
  padding-left: 2rem !important;
  margin-top: 2rem;
}

.navbar {
  background-color: white;
  height: 100px;
  display: flex;
  justify-content: start;
  align-items: center;
}
.navbar-toggle {
  height: 10rem !important;
  background-color: #eaeae1 !important;
}
.navbar-toggle > ol,
ul {
  padding-left: 0px !important;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}
.menu-bars {
  color: lightblue !important;
  text-decoration: none;
}
.menu-bars > p {
  color: grey;
  margin-top: 0.5rem;
  font-size: 1rem;
}
.nav-menu {
  background-color: white;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}
.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: grey;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #eaeae1;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: white;
  color: lightblue !important;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span {
  margin-left: 16px;
}

.blue > a > svg {
  color: #00dff9 !important;
}

.orange > a > svg {
  color: orange !important;
}

.red > a > svg {
  color: red !important;
}

.darkblue > a > svg {
  color: #0718f9 !important;
}
.yellow > a > svg {
  color: greenyellow !important;
}

.purple > a > svg {
  color: purple !important;
}
.black > a > svg {
  color: black !important;
}

.center {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 2.5rem;
}
.center h1 {
  color: lightblue !important;
}
.iconsProfile {
  text-align: center;
}
.linkedin > svg {
  color: #00dff9 !important;
}
.github > svg {
  color: black !important;
}
.iconProfile {
  font-size: 4rem;
}
.email > svg {
  color: red;
}
.phone {
  color: yellowgreen;
}

.experiences {
  margin: 8rem 5rem 0rem 5rem;
}
.educations {
  margin: 8rem 5rem 0rem 5rem;
}
.projectsdet {
  margin: 8rem 5rem 0rem 5rem;
}
.blogs {
  margin: 8rem 5rem 0rem 5rem;
}
.certificates {
  margin: 8rem 5rem 0rem 5rem;
}
.bloglist {
  margin-bottom: 10rem !important;
}
.container {
  margin: 26rem 10rem 7rem 10rem;
}
.blogsDetails {
  height: 22rem;
  margin-bottom: 3rem;
}
.blogsImg {
  height: 20rem !important;
}
.card-img-top {
  height: 10rem;
}

